import { gql } from '@apollo/client';

const GET_USER_OVERVIEW = gql`
  query getUserOverview {
    getUserOverview {
      Offers {
        Items {
          CounterOffer {
            BuyerCounter {
              OfferPrice
              SaleOfHomeContingency
              InspectionContingency
              FinanceContingency
              ClosingPeriod
              Furniture
            }
            SellerCounter {
              FinanceContingency
              InspectionContingency
              OfferPrice
              SaleOfHomeContingency
              ClosingPeriod
              Furniture
            }
          }
          Step {
            messages {
              buyer
            }
          }
          Contingencies {
            FinanceContingency
            FinanceContingencyDueDate
            FinanceContingencyRemoved
            FinanceContingencyRemovedAt
            InspectionContingency
            InspectionContingencyDueDate
            InspectionContingencyRemoved
            InspectionContingencyRemovedAt
            SaleOfHomeContingency
            SaleOfHomeContingencyDueDate
            SaleOfHomeContingencyRemoved
            SaleOfHomeContingencyRemovedAt
          }
          OfferNo
          OfferId
          Funding {
            DownPayment
            LoanType
            PurchaseType
          }
        }
      }
      Properties {
        Items {
          CreatedAt
          Offers {
            OfferId
            OfferNo
            SellerAccepted
            StepId
            CreatedAt
            CreatedBy
            UpdatedAt
            UpdatedBy
            Step {
              messages {
                seller
              }
            }
            CounterOffer {
              BuyerCounter {
                SaleOfHomeContingency
                OfferPrice
                FinanceContingency
                InspectionContingency
                ClosingPeriod
                Furniture
              }
              SellerCounter {
                SaleOfHomeContingency
                OfferPrice
                FinanceContingency
                InspectionContingency
                ClosingPeriod
                Furniture
              }
            }
            Contingencies {
              FinanceContingency
              FinanceContingencyDueDate
              FinanceContingencyRemoved
              FinanceContingencyRemovedAt
              InspectionContingency
              InspectionContingencyDueDate
              InspectionContingencyRemoved
              InspectionContingencyRemovedAt
              SaleOfHomeContingency
              SaleOfHomeContingencyDueDate
              SaleOfHomeContingencyRemoved
              SaleOfHomeContingencyRemovedAt
            }
            Funding {
              DownPayment
              LoanType
              PurchaseType
            }
          }
          PropertyNo
          PropertyId
          StatusId
          Thumbnail
          ListingDetails {
            ListPrice
          }
        }
      }
    }
  }
`;

