const getPropertiesQuery = ({ withStatus } = {}) =>
  `query getUserOverview {
    getUserOverview {
      Properties {
        Items {
          Thumbnail
          ListingDetails {
            ListPrice
          }
          PropertyData {
            FullAddress
          }
          ${withStatus ? 'StatusId' : ''}
        }
      }
    }
  }`;

const GET_PROPERTIES = gql`
  ${getPropertiesQuery()}
`;

const GET_PROPERTIES_WITH_STATUS = gql`
  ${getPropertiesQuery({ withStatus: true })}
`;

const GET_SELLER_DOCUMENTS = gql`
  query getUserOverview {
    getUserOverview {
      Properties {
        Items {
          AcceptedOffer {
            Documents {
              id
              documentType
            }
            OfferId
          }
          PropertyId
          PropertyData {
            FullAddress
          }
        }
      }
    }
  }
`;
const GET_BUYER_DOCUMENTS = gql`
  query getUserOverview {
    getUserOverview {
      Offers {
        Items {
          OfferId
          Documents {
            documentType
            id
          }
          Property {
            PropertyId
            PropertyData {
              FullAddress
            }
          }
        }
      }
    }
  }
`;

export {
  GET_USER_OVERVIEW,
  GET_PROPERTIES,
  GET_PROPERTIES_WITH_STATUS,
  GET_SELLER_DOCUMENTS,
  GET_BUYER_DOCUMENTS,
};
